@import url('https://fonts.googleapis.com/css?family=Open+Sans');
body {
  font-family: 'Open Sans', sans-serif;
  color: #000;
  background-color: #222222;
  font-size: 14pt;
  overflow-x: hidden;
  margin: 0;
}
a {
  color: #333333;
}
a:hover {
  color: #000;
}
#container {
  position: relative;
  width: 100%;
  height: 100vh;
  max-width: 1530px;
  margin: 0 auto;
}
#info-block {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -153px;
  margin-left: -153px;
  box-sizing: border-box;
  width: 306px;
  height: 306px;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 25px 48px;
  text-align: center;
  z-index: 50;
  cursor: move;
}
#info-block h1 {
  cursor: default;
  margin-top: 0;
}
#info-block h1 a {
  text-decoration: none;
}
#info-block #links {
  margin: 0 auto;
  text-align: left;
  cursor: default;
}
#instafeed {
  position: absolute;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  margin: 0 auto;
  z-index: 10;
}
#instafeed a {
  transition: all 0.4s ease;
  flex-grow: 1;
}
#instafeed a:hover {
  flex-grow: 2;
}
#instafeed a img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
